<template>
  <div :class="[inputGroupClass, classes]" :id="id + '__wrapper'">
    <slot></slot>
  </div>
</template>

<script>
export default {
  inheritAttrs: false,
  props: {
    name: {
      type: String,
      require: true
    },
    id: {
      type: String,
      require: true
    },
    classes: {
      type: [String, Array, Object],
      default: ''
    }
  },
  computed: {
    isEmpty () {
      const value = this.$store.getters.getValue(this.$props.name);
      return value === undefined || value === null || value === '';
    },
    inputGroupClass () {
      return {
        "botyglot-form--class-when-not-empty": !this.isEmpty,
        "botyglot-form--class-when-empty": this.isEmpty
      }
    },
  },
}
</script>

