<template>
  <ComponentWrapper :name="name" :classes="inputGroupClass" :id="id">

    <div v-if="prepend" class="input-block__prepend" :id="id + '__prepend'">
      <span v-html="prepend"></span>
    </div>

    <input v-bind="$attrs"
           :class="[inputClass, {'input--has-prepend': prepend, 'input--has-append': append }]"
           :id="id" v-model="inputValue" @focus="inputTouched=true" :name="name"/>

    <div v-if="append" class="input-block__append" :id="id + '__append'">
      <span v-html="append"></span>
    </div>

    <ErrorsPlaceholder v-if="displayError" :names="[name]"></ErrorsPlaceholder>
  </ComponentWrapper>
</template>


<script>
import { computed, watchEffect } from 'vue';
import ErrorsPlaceholder from "./errors_placeholder.vue";
import ComponentWrapper from "./component_wrapper.vue";

export default {
  components: { ComponentWrapper, ErrorsPlaceholder },
  inheritAttrs: false,
  props: {
    append: {
      type: String,
    },
    name: {
      type: String,
      required: true
    },
    id: {
      type: String,
      required: true
    },
    prepend: {
      type: String,
    },
    suggest_value: {
      type: [String, Boolean],
      required: false,
      default: false
    },
    display_error: {
      type: [String, Boolean],
      required: false,
      default: true
    }
  },
  setup (props) {
    const inputTouched = computed({
      get () {
        return this.$store.getters.getTouched(props.name);
      },
      set (value) {
        this.$store.commit('setTouched', {
          value: value,
          name: props.name
        });
      }
    });

    const inputValue = computed({
      get () {
        return this.$store.getters.getValue(props.name);
      },
      set (value) {
        this.$store.dispatch('update', {
          value: value,
          name: props.name
        });
      }
    });

    const suggestValue = computed(() => {
      return (props.suggest_value === 'true' || props.suggest_value === true || props.suggest_value === "force");
    });

    const displayError = computed(() => {
      return (props.display_error === 'true' || props.display_error === true);
    });

    const useSuggestedValue = computed(() => {
      return (suggestValue.value && !inputTouched.value) || props.suggest_value === 'force';
    });

    watchEffect(() => {
      if (useSuggestedValue.value) {
        inputValue.value = this.$store.getters.getSuggestedValues(props.name);
      }
    });

    const inputClass = computed(() => {
      return {
        "input-block__field": true,
        "input-block__field--invalid": (inputTouched.value && inputError.value),
        "input-block__field--warning": (inputTouched.value && !inputError.value && inputWarning.value)
      };
    });

    const inputError = computed(() => {
      if (inputTouched.value) {
        return this.$store.getters.getError(props.name);
      } else {
        return null;
      }
    });

    const inputWarning = computed(() => {
      if (inputTouched.value) {
        return this.$store.getters.getWarning(props.name);
      } else {
        return null;
      }
    });

    const inputGroupClass = computed(() => {
      return {
        "input-block": true
      };
    });

    return {
      inputTouched,
      inputValue,
      suggestValue,
      displayError,
      useSuggestedValue,
      inputClass,
      inputError,
      inputWarning,
      inputGroupClass
    };
  }
};
</script>
