<template>
  <input v-bind="$attrs" :id="id" :name="name" v-model="inputValue"/>
</template>


<script>
export default {
  inheritAttrs: false,
  props: {
    append: {
      type: String,
    },
    name: {
      type: String,
      require: true
    },
    id: {
      type: String,
      require: true
    },
    prepend: {
      type: String,
    },
    suggest_value: { // do not use directly (instead use the computed property suggestValue)
      type: [String, Boolean],
      require: false,
      default: false
    },
  },
  computed: {
    inputValue: {
      get () {
        return this.$store.getters.getValue(this.name)
      },
      set (value) {
        this.$store.dispatch('update', {
              value: value,
              name: this.name
            }
        )
      }
    },

    suggestValue () {
      return (this.suggest_value === 'true' || this.suggest_value === true)
    },

    useSuggestedValue () {
      return this.suggestValue
    },

    suggestedValue () {
      return this.$store.getters.getSuggestedValues(this.name)
    },
  },
  watch: {
    suggestedValue (value, _oldValue) {
      if (this.useSuggestedValue) {
        this.inputValue = value
      }
    },
  },
}
</script>

