<template>
  <span v-if="displaySuggestedValue" v-bind="this.$attrs">{{ getSuggestedValue }}</span>
</template>


<script>
export default {
  inheritAttrs: false,
  props: {
    name: {
      type: String,
      require: true
    },
  },
  computed: {
    getSuggestedValue () {
      return this.$store.getters.getSuggestedValues(this.$props.name)
    },
    displaySuggestedValue () {
      return this.getSuggestedValue !== null
    }
  },
}
</script>

